import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import Typography from '@material-ui/core/Typography';

const styles = theme => ({
  root: {
    margin: 'auto',
    overflowX: 'auto',
    [theme.breakpoints.down('sm')]: {
      width: '95%',
    },
    [theme.breakpoints.up('md')]: {
      width: '80%',
    },
    [theme.breakpoints.up('lg')]: {
      width: '80%',
    },
    marginBottom: theme.spacing.unit * 4,
  },
  title: {
    marginTop: theme.spacing.unit * 4,
    textAlign: 'center',
    marginBottom: theme.spacing.unit * 2,
  }
});

function CheeseTable(props) {
  const { classes, items, onDelete, onEdit } = props;

  return (
    <>
    <Typography variant="h4" className={classes.title}>
        Cheeses
      </Typography>
    <Paper className={classes.root}>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell align="left">Description</TableCell>
            <TableCell align="right">Rating</TableCell>
            <TableCell align="right"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {items.length === 0 ? 
          <TableRow key={0}>
          <TableCell colSpan={4}>
          <Typography variant="subtitle1" color="secondary" className={classes.title}>
            No Cheeses to display
          </Typography>
          </TableCell>
          </TableRow> : 
          items.map(item => (
            <TableRow key={item.id}>
              <TableCell component="th" scope="row">
                {item.name}
              </TableCell>
              <TableCell align="left">{item.description}</TableCell>
              <TableCell align="right">{item.rating}</TableCell>
              <TableCell align="right">
                <IconButton aria-label="Edit" onClick={() => onEdit(item)}>
                  <EditIcon />
                </IconButton>
                <IconButton aria-label="Delete" onClick={() => onDelete(item.id)}>
                  <DeleteIcon />
                </IconButton>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Paper>
    </>
  );
}

CheeseTable.propTypes = {
  classes: PropTypes.object.isRequired,
  items: PropTypes.array.isRequired,
  onDelete: PropTypes.func,
  onEdit: PropTypes.func
};

export default withStyles(styles)(CheeseTable);
