import React from 'react';
import { Redirect } from 'react-router-dom';
import PropTypes from 'prop-types';
import CheeseForm from './cheese/layout/CheeseForm';
import MenuForm from './menu/layout/MenuForm';
import { withStyles } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 4,
    paddingBottom: theme.spacing.unit * 4,
    marginTop: theme.spacing.unit * 8,
    margin: 'auto',
    [theme.breakpoints.down('sm')]: {
      width: '90%',
    },
    [theme.breakpoints.up('md')]: {
      width: '60%',
    },
    [theme.breakpoints.up('lg')]: {
      width: '45%',
    },
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center'

  },
  add: {
    margin: theme.spacing.unit * 2,
    marginTop: theme.spacing.unit * 4,
  }
});

class Main extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        toAddCheeseForm: false,
        toCheeses: false,
        toAddMenuForm: false,
        toMenus: false
      };
    }
    showAddCheese = () => this.setState({ toAddCheeseForm: true});
    showAddMenu = () => this.setState({ toAddMenuForm: true});

    handleAddCheese = (cheeseAdded) => {
      this.setState({ toAddCheeseForm: false});
      if(cheeseAdded) {
        this.setState({toCheeses: true});
      }
    }

    handleAddMenu = (menuAdded) => {
      this.setState({ toAddMenuForm: false});
      if(menuAdded) {
        this.setState({toMenus: true});
      }
    }

    render() {
      if(this.state.toCheeses) {
        return <Redirect to='/cheeses'/>
      }
      else if(this.state.toMenus) {
        return <Redirect to='/menus'/>
      }
      const { classes } = this.props;
      const { toAddCheeseForm, toAddMenuForm } = this.state;
      return (
        <>
        <Paper className={classes.root} elevation={1}>
          <Typography component="h3">
            This is a single page application that employs Cheese-API to perform CRUD operations. Cheese-API is a REST API written in Spring Boot.
          </Typography>
          <Button size="large" variant="contained" color="secondary" className={classes.add} onClick={()=> this.showAddCheese()}>Add Cheese</Button>
          <Button size="large" variant="contained" color="secondary" className={classes.add} onClick={()=> this.showAddMenu()}>Add Menu</Button>
        </Paper>
       
        
        {toAddCheeseForm && <CheeseForm handleClose={this.handleAddCheese} action="Add Cheese"/>}
        {toAddMenuForm && <MenuForm handleClose={this.handleAddMenu} action="Add Menu"/>}
        </>
      );
    }
  }

Main.propTypes = {
  classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(Main);