import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Paper from '@material-ui/core/Paper';


const styles = theme => ({
  root: {
    marginTop: theme.spacing.unit * 4,
    margin: 'auto',
    [theme.breakpoints.down('sm')]: {
      width: '95%',
    },
    [theme.breakpoints.up('md')]: {
      width: '70%',
    },
    [theme.breakpoints.up('lg')]: {
      width: '60%',
    },
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
  title: {
    textAlign: 'center',
    marginBottom: theme.spacing.unit * 2,
  },

  nomenu: {
    textAlign: 'center',
    marginBottom: theme.spacing.unit * 2,
    padding: theme.spacing.unit * 4,
  }
});

class MenuList extends React.Component {
  state = {
    expanded: null,
  };

  handleChange = panel => (event, expanded) => {
    this.setState({
      expanded: expanded ? panel : false,
    });
  };

  render() {
    const { classes, items } = this.props;
    const { expanded } = this.state;

    return (
      <div className={classes.root}>
      <Typography variant="h4" className={classes.title}>
        Menus
      </Typography>
      { items.length === 0 ? 
          <Paper>
          <Typography variant="subtitle1" color="secondary" className={classes.nomenu}>
            No Menus to display
          </Typography>
          </Paper> :  
      items.map(item => (
        <ExpansionPanel key={item.id} expanded={expanded === item.id} onChange={this.handleChange(item.id)}>
          <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
            <Typography variant="h6">{item.name}</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
          <Grid item xs={12} md={6}>
            <Typography variant="subtitle1">
             Cheeses
            </Typography>
            <div className={classes.demo}>
              <List>
                {item.cheeses.map((cheese, index) => (
                  <ListItem key={index}>
                    <ListItemText
                      primary={cheese.name}
                      secondary={cheese.description}
                    />
                  </ListItem>
                ))
              }
              </List>
            </div>
          </Grid>
          </ExpansionPanelDetails>
        </ExpansionPanel>
        )
        )}
      </div>
    );
  }
}

MenuList.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(MenuList);
